import React from 'react';
import Card from '@material-ui/core/Card';


export default function HomeCard(props) {
  return (
    <div class="lightbackground">
      <a href={props.href} style={{textDecoration:"none"}}>
        <img class="homeimg" src={"/images/" + props.myImg} />
        {makeCard(props)}
      </a>
    </div>
  );
}

function makeCard(props) {
  if (props.desc != null) {
    return (
      <Card class="homecard" href={props.href}>
        <h1 class="hometitle">{props.title}</h1>
        <p class="homedesc">{props.desc}</p>
      </Card> 
    );
  } else {
    return (
      <Card class="homecard" style={{paddingBottom:"2px"}} href={props.href}>
        <h1 class="hometitle">{props.title}</h1>
      </Card> 
    );
  }
}
